<template>
  <div class="content">
    <template v-for="(item, index) in fieldList">
      <div
        v-show="icon === 'close' ? index < count : true"
        :key="index"
      >
        <div class="item-style">
          <span class="label-style" :style="{width: width.labelWidth + 'px'}">{{ item.label }}</span>
          <!-- ÊôÆÈÄöËæìÂÖ•Ê°Ü -->
          <el-input
            v-if="item.type === 'input'"
            v-model.trim="defaultData[item.value]"
            :size="size"
            :style="{width: width.itemWidth + 'px'}"
            :type="item.type"
            :disabled="item.disabled"
            :placeholder="getPlaceholder(item)"
            @keyup.enter.native="handleFilter"
            @blur="handleEvent($event, item.value)"
          />
          <!-- Êó•Êúü/Êó∂Èó¥ -->
          <el-date-picker
            v-if="item.type === 'date'"
            v-model="defaultData[item.value]"
            :size="size"
            :style="{width: width.itemWidth + 'px'}"
            :type="item.dateType"
            :picker-options="item.TimePickerOptions"
            :clearable="item.clearable"
            :disabled="item.disabled"
            start-placeholder="ÂºÄÂßãÊó∂Èó¥"
            end-placeholder="ÁªìÊùüÊó∂Èó¥"
            :placeholder="getPlaceholder(item)"
            @change="handleEvent($event, item.value, 'change')"
          />
          <!-- ÈÄâÊã©Ê°Ü -->
          <el-select
            v-if="item.type === 'select'"
            v-model="defaultData[item.value]"
            :size="size"
            :style="{width: width.itemWidth + 'px'}"
            :disabled="item.disabled"
            :clearable="item.clearable"
            :filterable="item.filterable"
            :multiple="item.multiple"
            :placeholder="getPlaceholder(item)"
            @change="handleEvent($event, item.value, 'change')"
          >
            <el-option
              v-for="childItem in listTypeInfo[item.list]"
              :key="childItem.id"
              :label="childItem.name"
              :value="childItem.id"
              :disabled="childItem.disabled"
            />
          </el-select>
          <!-- ËÆ°Êï∞Âô® -->
          <el-input-number
            v-if="item.type === 'inputNumber'"
            v-model="defaultData[item.value]"
            :size="size"
            :style="{width: width.itemWidth + 'px'}"
            :min="item.min"
            :max="item.max"
            @change="handleEvent($event, item.value, 'change')"
          />
        </div>
      </div>
    </template>
      <div
        v-if="!btnHidden && fieldList.length !== 1"
        class="btn-style"
      >
        <el-button
          :type="btnStyle[0].type"
          :size="size"
          :plain="btnStyle[0].plain"
          :round="btnStyle[0].round"
          :icon="btnStyle[0].icon"
          :disabled="btnStyle[0].disabled"
          @click="handleFilter"
        >
          {{ btnStyle[0].text }}
        </el-button>
        <el-button
          :type="btnStyle[1].type"
          :size="size"
          :plain="btnStyle[1].plain"
          :round="btnStyle[1].round"
          :disabled="btnStyle[1].disabled"
          :icon="btnStyle[1].icon"
          @click="handleReset"
        >
          {{ btnStyle[1].text }}
        </el-button>
      </div>
      <el-button
        v-if="!btnHidden && fieldList.length > count"
        type="text"
        @click="icon === 'open' ? icon = 'close' : icon = 'open'"
        style="margin-left: 6px;"
      >{{ icon === 'open' ? 'Êî∂Ëµ∑' : 'Â±ïÂºÄ' }}
        <i :class="icon === 'open' ? 'el-icon-caret-top' : 'el-icon-caret-bottom'" />
      </el-button>
  </div>
</template>
<script>
/**
 * @author üåàMARS <wangdaoo66@gmail.com>
 * @desc üìùvue + element-ui ‰∏≠ÂêéÂè∞ÊêúÁ¥¢ÁªÑ‰ª∂
 * @copyright ü§ù2020 Â∞Ω‰∫∫‰∫ã, Ë°åÁéãÈÅì
 */
export default {
    name: 'ElFilter',
    props: {
      /**Â≠óÊÆµÈªòËÆ§Êï∞ÊçÆ */
      data: {
        type: Object,
        default: () => {},
        required: true
      },
      /**Â≠óÊÆµÈÖçÁΩÆÈ°π */
      fieldList: {
        type: Array,
        default: () => [],
        required: true
      },
      /**Áõ∏ÂÖ≥ÁöÑÂàóË°® */
      listTypeInfo: {
        type: Object,
        default: () => {}
      },
      /**ÊåâÈíÆÂå∫ÂüüÊòØÂê¶ÈöêËóè */
      btnHidden: {
        type: Boolean,
        default: false
      },
      /**ÁªÑ‰ª∂Â∞∫ÂØ∏ */
      size: {
        type: String,
        default: 'mini'
      },
      /**ÈªòËÆ§ÊêúÁ¥¢Êï∞ */
      count: {
        type: Number,
        default: 4
      },
      /**ÁªÑ‰ª∂ÂèälabelÂÆΩÂ∫¶ */
      width: {
        type: Object,
        default: () => ({
          labelWidth: 110,
          itemWidth: 220
        })
      },
      /**ÊåâÈíÆÈÖçÁΩÆ */
      btnStyle: {
        type: Array,
        default: () => [
          { icon: null, text: 'ÊêúÁ¥¢', disabled: false, type: 'primary', plain: false, round: false },
          { icon: null, text: 'ÈáçÁΩÆ', disabled: false, type: null, plain: false, round: false }
        ]
      }
    },
    data: () => ({
      defaultData: {},
      icon: 'close'
    }),

    mounted () {
      /**
       * Â≠êÁªÑ‰ª∂Êó†Ê≥ïÁõ¥Êé•‰øÆÊîπÁà∂ÁªÑ‰ª∂‰º†ÈÄíËøáÊù•ÁöÑÂÄº
       * ‰∫éÊòØÂ∞ÜÁà∂ÁªÑ‰ª∂‰º†ÈÄíÁöÑÂÄºÈ¶ñÂÖàËµãÂÄºÁªô defaultData
       * Âú®ÊêúÁ¥¢Êù°‰ª∂‰∏≠ÂêåÊ†∑‰ΩøÁî® defaultData
       * Ê∞∏Ëøú‰øùÊåÅ props ‰º†ÈÄíÁöÑ data Á∫ØÊ¥ÅÂ∫¶
       */
      this.defaultData = {...this.data}
    },

    methods: {
        /**
         * @func Âç†‰ΩçÁ¨¶ÊòæÁ§∫
         * @param {Object} row
         * @desc üìù
         */
        getPlaceholder (row) {
          let placeholder
          if (row.type === 'input') {
            placeholder = 'ËØ∑ËæìÂÖ•' + row.label
          } else if (row.type === 'select' || row.type === 'time' || row.type === 'date') {
            placeholder = 'ËØ∑ÈÄâÊã©' + row.label
          } else {
            placeholder = row.label
          }
          return placeholder
        },

        /**
         * @func ‰∫ã‰ª∂Â§ÑÁêÜ
         * @desc üìù
         */
        handleEvent (event, val, change) {
          let obj = {
            value: change === 'change' ? event : event.target.value,
            label: val
          }
          this.$emit('handleEvent', obj)
        },

        /**
         * @func ÊêúÁ¥¢
         * @desc üìù
         */
        handleFilter () {
          this.$emit('handleFilter', this.defaultData)
        },

        /**
         * @func ÈáçÁΩÆ
         * @desc üìù
         */
        handleReset () {
          this.defaultData = {...this.data}
          this.$emit('handleReset', this.defaultData)
        }
    }
}
</script>
<style lang="css" scoped>
.content {
  display: flex;
  flex-wrap: wrap;
  position: relative;
}

.content .item-style {
  margin: 6px auto;
  line-height: 1;
}
.content .item-style .label-style {
  display: inline-block;
  justify-self: end;
  font-size: 13px;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  text-align: right;
  margin-right: 12px;
  color: #222222;
}
.btn-style {
  margin: 6px 0 6px auto;
}
</style>

